* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: 'Ubuntu'; }
  @media only screen and (max-width: 450px) {
    body {
      height: auto;
      display: block;
      padding: 0; } }

header {
  background: #0D1621;
  color: white; }
  header nav {
    max-width: 900px;
    margin: auto;
    padding: 15px;
    display: grid;
    grid-template-columns: 1fr auto;
    justify-items: start; }
    @media only screen and (max-width: 720px) {
      header nav {
        grid-template-columns: 1fr;
        grid-gap: 20px;
        padding: 25px 15px; } }
    header nav svg {
      height: 60px;
      width: 60px;
      color: white;
      fill: white; }
  header nav ul {
    display: flex;
    align-items: center;
    justify-content: start;
    list-style-type: none; }
    @media only screen and (max-width: 690px) {
      header nav ul {
        flex-direction: column;
        align-items: flex-start; } }
    header nav ul li {
      display: block;
      margin-right: 25px;
      line-height: 30px; }
      header nav ul li:last-child {
        margin-right: 0; }
      header nav ul li a {
        text-decoration: none;
        position: relative;
        color: rgba(255, 255, 255, 0.3);
        font-size: 17px;
        z-index: 0;
        cursor: pointer; }
        header nav ul li a.active {
          color: white; }
      header nav ul li a:before {
        position: absolute;
        color: #FFF;
        top: 0px;
        left: 0px;
        padding: 0px;
        overflow: hidden;
        content: attr(alt);
        transition: all 0.3s;
        transform: scale(0.8);
        opacity: 0; }
      header nav ul li a:hover:before {
        transform: scale(1);
        opacity: 1; }

main {
  margin: auto; }

figure {
  color: #6a737d;
  font-size: 0.9em;
  margin: 10px 0; }
  figure img {
    width: 100%; }

.markdown-body {
  max-width: 900px;
  margin: 30px auto; }

.footer {
  display: flex;
  align-items: center;
  padding: 30px 30px;
  font-size: 20px;
  justify-content: space-between;
  background: #0D1621;
  color: white; }
  .footer a {
    color: white; }
  .footer span {
    padding: 0 10px; }
    .footer span:first-of-type {
      font-size: 14px; }
